// Breadcrumbs
.breadcrumb {
    font-size: 18px;
    color: rgba(255,255,255, .7);

    i,
    [class^="mdi-"], [class*="mdi-"],
    i.material-icons {
        display: inline-block;
        float: left;
        font-size: 24px;
    }

    &:before {
        content: '\E5CC';
        color: rgba(255,255,255, .7);
        vertical-align: top;
        display: inline-block;
        font-family: 'Material Icons';
        font-weight: normal;
        font-style: normal;
        font-size: 25px;
        margin: 0 10px 0 8px;  
        -webkit-font-smoothing: antialiased;
    }

    &:first-child:before {
        display: none;
    }

    &:last-child {
        color: #fff;
    }
}
