/* ==========================================================================
   $BASE-DATE-PICKER
   ========================================================================== */
/**
 * The picker box.
 */
.picker__box {
    padding: 0;
    border-radius: 2px;
    overflow: hidden;
}
/**
 * The header containing the month and year stuff.
 */
.picker__header {
    text-align: center;
    position: relative;
    margin-top: .75em;
}
/**
 * The month and year labels.
 */
.picker__month,
.picker__year {
    //  font-weight: 500;
    display: inline-block;
    margin-left: .25em;
    margin-right: .25em;
}
/**
 * The month and year selectors.
 */
.picker__select--month,
.picker__select--year {

    height: 2em;
    padding: 0;
    margin-left: .25em;
    margin-right: .25em;
}

// Modified
.picker__select--month.browser-default {
    display: inline;
    background-color: #FFFFFF;
    width: 40%;
}
.picker__select--year.browser-default {
    display: inline;
    background-color: #FFFFFF;
    width: 26%;
}
.picker__select--month:focus,
.picker__select--year:focus {
    border-color: $datepicker-focus;
}
/**
 * The month navigation buttons.
 */
.picker__nav--prev,
.picker__nav--next {
    position: absolute;
    padding: .5em 1.25em;
    width: 1em;
    height: 1em;
    box-sizing: content-box;
    top: -0.25em;
}
//@media (min-width: 24.5em) {
//  .picker__nav--prev,
//  .picker__nav--next {
//    top: -0.33em;
//  }
//}
.picker__nav--prev {
    left: -1em;
    padding-right: 1.25em;
}
//@media (min-width: 24.5em) {
//  .picker__nav--prev {
//    padding-right: 1.5em;
//  }
//}
.picker__nav--next {
    right: -1em;
    padding-left: 1.25em;
}
//@media (min-width: 24.5em) {
//  .picker__nav--next {
//    padding-left: 1.5em;
//  }
//}

.picker__nav--disabled,
.picker__nav--disabled:hover,
.picker__nav--disabled:before,
.picker__nav--disabled:before:hover {
    cursor: default;
    background: none;
    border-right-color: #f5f5f5;
    border-left-color: #f5f5f5;
}
/**
 * The calendar table of dates
 */
.picker__table {
    text-align: center;
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
    font-size: 1rem;
    width: 100%;
    margin-top: .75em;
    margin-bottom: .5em;
}



.picker__table th, .picker__table td {
    text-align: center;
}






.picker__table td {
    margin: 0;
    padding: 0;
}
/**
 * The weekday labels
 */
.picker__weekday {
    width: 14.285714286%;
    font-size: .75em;
    padding-bottom: .25em;
    color: #999999;
    font-weight: 500;
    /* Increase the spacing a tad */
}
@media (min-height: 33.875em) {
    .picker__weekday {
        padding-bottom: .5em;
    }
}
/**
 * The days on the calendar
 */

.picker__day--today {
    position: relative;
    color: #595959;
    letter-spacing: -.3;
    padding: .75rem 0;
    font-weight: 400;
    border: 1px solid transparent;

}

//.picker__day--today:before {
//  content: " ";
//  position: absolute;
//  top: 2px;
//  right: 2px;
//  width: 0;
//  height: 0;
//  border-top: 0.5em solid #0059bc;
//  border-left: .5em solid transparent;
//}
.picker__day--disabled:before {
    border-top-color: #aaaaaa;
}


.picker__day--infocus:hover{
    cursor: pointer;
    color: #000;
    font-weight: 500;
}

.picker__day--outfocus {
    display: none;
    padding: .75rem 0;
    color: #fff;

}
.picker__day--outfocus:hover {
    cursor: pointer;
    color: #dddddd;
    //  background: #b1dcfb;
    font-weight: 500;
}


.picker__day--highlighted {
    //  border-color: #0089ec;
}
.picker__day--highlighted:hover,
.picker--focused .picker__day--highlighted {
    cursor: pointer;
    //  color: #000000;
    //  background: #b1dcfb;
    //  font-weight: 500;
}
.picker__day--selected,
.picker__day--selected:hover,
.picker--focused .picker__day--selected {


    //  Circle background
    border-radius: 50%;
    transform: scale(.75);
    background: #0089ec;
    color: #ffffff;
}
.picker__day--disabled,
.picker__day--disabled:hover,
.picker--focused .picker__day--disabled {
    background: #f5f5f5;
    border-color: #f5f5f5;
    color: #dddddd;
    cursor: default;
}
.picker__day--highlighted.picker__day--disabled,
.picker__day--highlighted.picker__day--disabled:hover {
    background: #bbbbbb;
}
/**
 * The footer containing the "today", "clear", and "close" buttons.
 */
.picker__footer {
    text-align: right;
}
.picker__button--today,
.picker__button--clear,
.picker__button--close {
    border: 1px solid #ffffff;
    background: #ffffff;
    font-size: .8em;
    padding: .66em 0;
    font-weight: bold;
    width: 33%;
    display: inline-block;
    vertical-align: bottom;
}
.picker__button--today:hover,
.picker__button--clear:hover,
.picker__button--close:hover {
    cursor: pointer;
    color: #000000;
    background: #b1dcfb;
    border-bottom-color: #b1dcfb;
}
.picker__button--today:focus,
.picker__button--clear:focus,
.picker__button--close:focus {
    background: #b1dcfb;
    border-color: $datepicker-focus;
    outline: none;
}
.picker__button--today:before,
.picker__button--clear:before,
.picker__button--close:before {
    position: relative;
    display: inline-block;
    height: 0;
}
.picker__button--today:before,
.picker__button--clear:before {
    content: " ";
    margin-right: .45em;
}
.picker__button--today:before {
    top: -0.05em;
    width: 0;
    border-top: 0.66em solid #0059bc;
    border-left: .66em solid transparent;
}
.picker__button--clear:before {
    top: -0.25em;
    width: .66em;
    border-top: 3px solid #ee2200;
}
.picker__button--close:before {
    content: "\D7";
    top: -0.1em;
    vertical-align: top;
    font-size: 1.1em;
    margin-right: .35em;
    color: #777777;
}
.picker__button--today[disabled],
.picker__button--today[disabled]:hover {
    background: #f5f5f5;
    border-color: #f5f5f5;
    color: #dddddd;
    cursor: default;
}
.picker__button--today[disabled]:before {
    border-top-color: #aaaaaa;
}

/* ==========================================================================
   CUSTOM MATERIALIZE STYLES
   ========================================================================== */
/*.picker__box {
  border-radius: 2px;
  overflow: hidden;
}*/

.picker__date-display {
    text-align: left;
    background-color: $datepicker-date-bg;
    color: #fff;
    padding: 18px;
    font-weight: 300;
}

@media only screen and (min-width: 601px) {
    .picker__date-display {
	flex:1;
    }
    .picker__weekday-display {
	display:block;
    }
    .picker__container__wrapper {
	flex:2
    }
}

.picker__nav--prev:hover,
.picker__nav--next:hover {
    cursor: pointer;
    color: #000000;
    background: $datepicker-selected-outfocus;
}

.picker__weekday-display {
    font-weight: 500;
    font-size: $datepicker-display-font-size;
    margin-right: 5px;
    margin-top: 4px;
}

.picker__month-display {
    //text-transform: uppercase;
    font-size: $datepicker-display-font-size;
    font-weight: 500;
}
.picker__day-display {
    font-size: $datepicker-display-font-size;
    font-weight: 500;
    margin-right: 5px;
}
.picker__year-display {
    font-size: 1.5rem;
    font-weight: 500;
    color: $datepicker-year;
}

/*.picker__box {
  padding: 0;
}*/
.picker__calendar-container {
    padding: 0 1rem;

    thead {
        border: none;
    }
}

// Calendar
.picker__table {
    margin-top: 0;
    margin-bottom: .5em;
}

.picker__day--infocus {
    color: $datepicker-weekday-color;
    letter-spacing: -.3px;
    padding: 0.75rem 0;
    font-weight: 400;
    border: 1px solid transparent;
}
@media only screen and (min-width: 601px) {
    .picker__day--infocus {
	padding: 1.1rem 0;
    }
}


//Today style
.picker__day.picker__day--today {
    color: $datepicker-selected;
}

.picker__day.picker__day--today.picker__day--selected {
    color: #fff;
}

// Table Header
.picker__weekday {
    font-size: .9rem;
}


.picker__day--selected,
.picker__day--selected:hover,
.picker--focused .picker__day--selected {
    // Circle background
    border-radius: 50%;
    transform: scale(.9);
    background-color: $datepicker-selected;
    &.picker__day--outfocus {
        background-color: $datepicker-selected-outfocus;
    }
    color: #ffffff;
}

.picker__footer {
    text-align: right;
    padding: 5px 10px;
}

// Materialize modified
.picker__close, .picker__today, .picker__clear {
    border-color: $link-color;
    background-color: #fff;
    font-size: 1.1rem;
    padding: 0 1rem;
    color: $datepicker-selected;
}
.picker__clear {
    border-color: #f44336;
    color:#f44336;
    float:left;
}

//month nav buttons
.picker__nav--prev:before,
.picker__nav--next:before {
    content: " ";
    border-top: .5em solid transparent;
    border-bottom: .5em solid transparent;
    border-right: 0.75em solid #676767;
    width: 0;
    height: 0;
    display: block;
    margin: 0 auto;
}
.picker__nav--next:before {
    border-right: 0;
    border-left: 0.75em solid #676767;
}
button.picker__today:focus, button.picker__clear:focus, button.picker__close:focus {
    background-color: $datepicker-selected-outfocus;
}
