// 1. Colors
// ==========================================================================
$primary-color: color("blue", "base") !default;
$primary-color-light: color("blue", "light") !default;
$primary-color-dark: color("blue", "dark") !default;

$secondary-color: color("orange", "base") !default;
$secondary-color-light: color("orange", "light") !default;
$secondary-color-dark: color("orange", "dark") !default;

$link-color: color("blue", "accent") !default;
$cta-color: color("orange", "accent") !default;

$success-color: color("green", "base") !default;
$error-color: color("red", "base") !default;
$white-color-dark: #f2f2f2 !default;
$white-color-off: #f7f7f7 !default;
$black-color-light: #999 !default;


$blue-color-dark: #3173a5 !default;
$underline-color: $blue-color-dark;


// 2. Badges
// ==========================================================================
$badge-bg-color: $secondary-color !default;
$badge-height: 22px !default;


